:root {
  --primary-text-color: #86e543;
  --secondary-text-color: #fff;
  --third-text-color: # #8d9289;
}

html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.18;
}

@media screen and (min-width: 768px) {
  body {
    font-size: 24px;
    font-weight: 400;
    line-height: 1.16;
  }
}

@media screen and (min-width: 1280px) {
  body {
    font-size: 24px;
    font-weight: 400;
    line-height: .85;
  }
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 16px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 40px;
  }
}

@media screen and (min-width: 1280) {
  .container {
    width: 1280px;
  }
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

a {
  text-decoration: none;
}

.list {
  list-style: none;
}

.btn {
  text-align: center;
  border-color: rgba(0, 0, 0, 0);
  font-family: inherit;
}

.btn:hover {
  cursor: pointer;
}

