//************************
// GENERAL
//************************

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.18;

  @media screen and (min-width: 768px) {
    font-weight: 400;
    font-size: 24px;
    line-height: 1.16;
  }

  @media screen and (min-width: 1280px) {
    font-weight: 400;
    font-size: 24px;
    line-height: 0.85;
  }
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 16px;
  // outline: 2px solid tomato;

  @media screen and (min-width: 480px) {
    width: 480px;
  }
  @media screen and (min-width: 768px) {
    padding: 0 40px;
    width: 768px;
  }
  @media screen and(min-width: 1280) {
    width: 1280px;
  }
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

.logo {
}

a {
  text-decoration: none;
}

.list {
  list-style: none;
}

.btn {
  font-family: inherit;
  text-align: center;
  border-color: transparent;
  &:hover {
    cursor: pointer;
  }
}
